var check = require('../../common/utils.js');
	import { debounce } from '@/common/utils.js';
export default {
    data() {
        return {
            email: '',
            emailCode: '',
			n:true,
            codename: ' 获取验证码',
            disabled: false
        };
    },
    onBackPress(option) {
        plus.key.hideSoftKeybord();
    },
	computed: {
		allowLogin () {
			return !!(this.email && this.emailCode)
		},
	},
    methods: {
		next: function() {
			var that=this;
			that.n = false;
		},
		back: function() {
			var that=this;
			that.n = true;
		},
        getEmailValue: function(e) {
            this.email = e.detail.value;
        },
        getEmailStyle: function(e) {
            var flag = check.checkEmail(this.email);
			if (!this.email) {
				uni.showToast({
				    title: '请输入邮箱',
				    icon: 'none',
				    duration: 2000
				});
				return false;
			}
            if (this.email && !flag) {
                uni.showToast({
                    title: '邮箱格式不正确',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
        },
        getEmailCode: function(e) {
            this.emailCode = e.detail.value;
        },
		linkToTransfer: debounce(function(){
			var that = this;
			if (!that.email) {
			    uni.showToast({
			        title: '请输入邮箱',
			        icon: 'none',
			        duration: 2000
			    });
			    return false;
			}
			var flag = check.checkEmail(that.email);
			if (!flag) {
			    uni.showToast({
			        title: '邮箱格式不正确',
			        icon: 'none',
			        duration: 2000
			    });
			    return false;
			}
			uni.request({
			    //绑定邮箱验证码
			    url: this.url + 'bindings/',
			    method: 'POST',
			    data: {
			        email: that.email
			    },
			    success: function(res) {
			        if (res.statusCode == 200) {
			            var num = 121;
			            var timer = setInterval(function() {
			                num--;
			                if (num <= 0) {
			                    clearInterval(timer);
			                    (that.codename = '重新发送'), (that.disabled = false);
			                } else {
			                    that.codename = num + 's';
			                    that.disabled = true;
			                }
			            }, 1000);
			        }
			        if (res.statusCode == 401) {
			            uni.showToast({
			                title: '该邮箱已被使用',
			                icon: 'none',
			                duration: 2000
			            });
			            return false;
			        }
			    },
			    fail: function(err) {}
			});
		},500, true),
        sendcode: function() {
			this.linkToTransfer()
        },
        save: function() {
            if (this.email == '') {
                uni.showToast({
                    title: '请输入邮箱地址',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            if (this.emailCode == '') {
                uni.showToast({
                    title: '请输入验证码',
                    icon: 'none',
                    duration: 2000
                });
                return false;
            }
            uni.request({
                url: this.url + 'linkemails/',
                method: 'POST',
                data: {
                    email: this.email,
                    email_msg: this.emailCode
                },
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 200) {
                       uni.navigateBack({
                       	  delta:2
                       });
						uni.showToast({
						    title: '邮箱绑定成功',
						    icon: 'none',
						    duration: 1500
						});
                    }
                    if (res.statusCode == 400) {
                        uni.showToast({
                            title: '验证码错误',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                }
            });
        }
    }
};
